<template>
  <div :class="[`input-${inputType}`, `${styleType}`, `${className}`, {'test': testClass}]">
    <!-- methods ÏÇ¨Ïö©Ìï†Îïå () ÍπåÏßÄ Ï†ÅÏñ¥ÏïºÌïúÎã§, ÏóÜÏñ¥ÎèÑ ÎπºÎ©¥ ÏãúÌñâÏïàÎê® -->
    <label 
        v-if="!isLabelHide()"
        :for="id"
        class="label-legend"><slot /></label>
    <!-- Î∂ÄÎ™®ÏóêÏÑú v-modelÎ°ú ÏÑ§Ï†ïÌïòÏó¨ ÏûêÎèôÏúºÎ°ú Î∞∞ÏπòÎêú valueÍ∞í Ìï†Îãπ -->
    <!-- Í∑ºÎç∞ Í∞ôÏùÄ Ïª¥Ìè¨ÎÑåÌä∏ Ïì∞ÎãàÍπê, Í∞ôÏù¥ Ïù¥Î≤§Ìä∏Í∞Ä Î∞úÏÉùÌï¥Î≤ÑÎ¶¨ÎÑ§??, Ïù¥Î¶ÑÎ∂ÄÎ∂ÑÎßå Ïù¥Î≤§Ìä∏ ÏûàÏùå, Ïù¥Î¶ÑÎßå ÏßÅÌòÄÏïºÏßÄ Ïôú Ïù¥Î©îÏùºÎèÑ Í∞ôÏù¥ Ï∞éÌòÄ Î®∏Î¶¨ ÏïàÎèåÏïÑÍ∞ÄÍ≤å -->
    <input 
        :id="id"
        :name="name"
        :type="inputType"
        :placeholder="placeholder"
        :readonly="readonly"
        :value="value"
        @input="onInputUpdate"/>

    <!-- [TODO]
        - ÏóêÎü¨Î¨∏Íµ¨ Ïª®Ìè¨ÎÑåÌä∏Ìôî
        - ÏóêÎü¨Î¨∏Íµ¨ Ïä§ÌÉÄÏùº
        - ÏóêÎü¨Î¨∏Íµ¨ Îì±Ïû•Ìö®Í≥º
     -->
    <p v-show="this.tmpErrMsg" class="error"><i></i>{{printErrMsg()}}</p>
  </div>
</template>
<script>
export default {
    name: 'InputText',
    data() {
        return {
            tmpErrMsg: null
        }
    },
    props: {
        id: {
            type: String,
            default: 'tmp'
        },
        name: {
            type: String,
            default: 'tmp'
        },
        inputType: {
            type: String,
            default: 'text'
        },
        styleType: {
            type: String,
            default: null
        },
        className: {
            type: String,
            default: null
        },
        placeholder: {
            type: String,
            default: 'ÌÖçÏä§Ìä∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî'
        },
        readonly: {
            type: Boolean,
            default: false
        },
        value: {
            type: String,
        },
        rules: {
            type: Array,
        },
        testClass: {
            type: Boolean,
        }
    },
    methods: {
        isLabelHide: function() {
            switch (this.styleType) {
                case 'type3':
                    return true;
                default:
                    return false;
            }
        },
        onInputUpdate: function(e) {
            // this.value = e.target.value
            // console.log(e.target.value, this.value, this.rules);
            // this.test(e.target.value);
            // this.checkRules();
            // ÏûêÏãùÏóêÏÑú ÏûÖÎ†•Îêú Í∞íÏùÑ Î∂ÄÎ™® Ïù¥Î≤§Ìä∏Î°ú Î≥¥ÎÉÑ
            // this.checkRules(value)
            this.$emit('input', e.target.value);
        },
        test: v => v.length <= 2 || 'Max 2 characters',
        checkRules: function() {
            // console.log(this.rules);
            let tmpValue = this.value;
            let tmpResult = this.rules.map(function(elem) {
                // console.log("foreach", elem(tmpValue), typeof(elem(tmpValue)));
                return elem(tmpValue);
                // elem('test');
            });
            // console.log("Í≤∞Í≥º", tmpResult);
            return tmpResult;
        },
        printErrMsg: function() {
            this.tmpErrMsg = this.checkRules().find(element=> element !== true );
            // console.log("print============", this.tmpErrMsg);
            return this.tmpErrMsg;
        }
    },
}
</script>
<style lang="scss">
.input-text {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .label-legend {
        font-size: 1.6rem;
        margin-bottom: 0.5em;
    }
    &.type1 {
        .label-legend {
            display: block;
            
        }
    }
    &.type2 {
        .label-legend {
            // justify Ïù¥Í≤ÉÏ†ÄÍ≤É Îã§ ÌñàÎäîÎç∞ Ïôú Ï†ÅÏö© Ïôú ÏïàÎèº..
            width: 100px;
            text-align: right;
            padding-right: 1.5rem;
        }
        input {
            width: calc(100% - 100px);
        }
        .error {
            margin-left: 10rem;
        }
    }
}
// [TODO] ÏóêÎü¨ Ïª¥Ìè¨ÎÑåÌä∏Ïóê ÎÑ£Í∏∞
.error {
    position: relative;
    display: block;
    margin-top: 1.5rem;
    color: $errColor01;
}
</style>