<template>
  <div :class="[`input-${inputType}`, `${styleType}`, `${className}`]">
    <input 
        :id="id"
        :type="inputType"
        @change="onChangeChecked" />

    <!-- [TODO]
        - ÏóêÎü¨Î¨∏Íµ¨Îäî Í∑∏Î£πÏóêÏÑú
        - ÌÖåÏä§Ìä∏ Ï£ºÏÑùÏùÄ ÎÖ∏Ìä∏ Ï†ïÎ¶¨ÎêòÎ©¥ Îã§ ÏßÄÏö∞Í∏∞
     -->
    <label 
        :for="id"
        class="label-legend">
      <slot />
    </label>
  </div>
</template>
<script>
export default {
    name: 'InputCheck',
    props: {
        id: {
            type: String,
            default: 'tmp'
        },
        name: {
            type: String,
            default: 'tmp'
        },
        inputType: {
            type: String,
            default: 'text'
        },
        styleType: {
            type: String,
            default: 'type1'
        },
        className: {
            type: String,
            default: null
        },
        placeholder: {
            type: String,
            default: 'ÌÖçÏä§Ìä∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî'
        },
        val: {
            type: String,
        },
        value: {
            // input checkboxÏùò checked Ïó¨Î∂ÄÏóê Îî∞Îùº  v-model value Í∞íÏúºÎ°ú Îì§Ïñ¥Í∞ÑÎã§.
            // Î∂ÄÎ™®ÏóêÏÑú Ìï¥Îãπ Ïù∏ÏûêÎ•º ÏßÅÏ†ëÏ†ÅÏúºÎ°ú Î≥¥ÎÇ¥ÏßÄ ÏïäÏïòÎã§. v-modelÏù¥ ÏàòÌñâÎê®Ïóê Îî∞Îùº Î≥ÄÍ≤ΩÎêú value Í∞íÏùÑ Î≥¥ÎÇ∏Í≤É.
            // type: ÏïàÏì∞Î©¥ errÎÇ¨ÏõÄ
            type: [Array, Boolean],
        }
    },
    methods: {
        onChangeChecked: function(e) {
            // this.value : Ï≤òÏùåÏùÄ Í≥µÎûÄ object v-modelÏóêÏÑú ÎπàÍ∞íÏù¥ ÎÑòÏñ¥Ïò¥ ÏïÑÎ¨¥Í≤ÉÎèÑ ÏßÄÏ†ïÌïúÍ≤å ÏóÜÏúºÎãà, Í∑∏ Ïù¥ÌõÑÏóî input ÏúºÎ°ú Î≥¥ÎÇ∏ arr Í∞íÏù¥ ÎÑòÏñ¥Ïò¥
            // pushÎ•º Ìï†Îïå value Í∞íÏùÑ ÎÑ£Îäî Í≤ÉÏù¥ ÏïÑÎãàÎùº, val Í∞íÏùÑ ÎÑ£ÎäîÎã§. valueÎäî Ïù¥ÎØ∏ v-modelÏóêÏÑú vueÏóêÏÑú ÏÇ¨Ïö©ÌïòÎäî Í∞íÏù¥Í∏∞ ÎïåÎ¨∏Ïóê, Î¨¥ÏãúÎêúÎã§. Í∑∏ÎûòÏÑú ÏûÑÏùòÏùò Î≥ÄÏàò valÏùÑ ÏÇ¨Ïö©Ìï¥ÏÑú push Ìï¥Ï§¨Ïùå.
            // vueÏóêÏÑú Í∏∞Î≥∏ÏúºÎ°ú ÏÑ§Ï†ïÌïú, model binding valueÎûë eventÎûë ÏïàÍ≤πÏπòÍ≤å v-model ÏÑ§Ï†ïÏùÑ Î≥ÄÍ≤ΩÌïòÎäî Í≤ÉÎèÑ ÏûàÏóàÎäîÎç∞, ÎÇòÏ§ëÏóê ÏûëÏóÖÌïòÍ∏∞
            // Ïôú Î≥ÑÎèÑÏùò Î≥ÄÏàòÎ•º Ïì∞ÎäêÎÉê? v-modelÏóêÏÑú ÎÑòÏñ¥Ïò® Î∂ÄÎ™®Ïùò value Í∞íÏùÑ ÏûêÏãùÏóêÏÑú Î≥ÄÍ≤ΩÌïòÎ©¥ ÏïàÎêòÍ∏∞ ÎïåÎ¨∏Ïóê, ÏûêÏãùÏóêÏÑú ÏÑ†ÌÉùÎêú Í∞íÏùÑ Î∞∞Ïó¥Ïóê Ï†ÄÏû•ÌïòÍ≥† Ìï¥Îãπ Î∞∞Ïó¥ÏùÑ Ïù¥Î≤§Ìä∏Î°ú pass ÌïúÍ≤É!, Ìï¥Îãπ Ïù¥Î≤§Ìä∏Îäî Î∂ÄÎ™®ÏóêÏÑú valueÎûë Ïó∞Í≤∞ÎêòÏñ¥ÏûàÏõÄ

            let checkedData = this.value;
            console.log(typeof(this.value));
            // this.$emit('input', this.val);
            // console.log('checked',e.target.checked);
            // console.log('val', this.val);
            // console.log('value',this.value); // ÏïàÎÇòÏò¥
            // console.log('event', e); // change
            // console.log('this', this); // Vue Ïª¥Ìè¨ÎÑåÌä∏
            // console.log('this.$attr', this.$attrs); // Vue Ïª¥Ìè¨ÎÑåÌä∏
            // console.log('this.$attr.value', this.$attrs.value); // Vue Ïª¥Ìè¨ÎÑåÌä∏
            // console.log('checked', e.target.checked)
            
            if (e.target.checked) {
                // e.target.value = on
                // console.log("e.target.value", e.target.value);
                // console.log("checkedData", checkedData);

                // ÏÑ†ÌÉùÌïú Í∞í ÏÇΩÏûÖ, ÌõÑ v-model input eventÏóê ÏÇΩÏûÖ array Î≥¥ÎÉÑ,
                // Í∑∏Îüº v-model inputÏóêÏÑú Ìï¥Îãπ Î∞∞Ïó¥ÏùÑ valueÍ∞íÏóê ÏÇΩÏûÖÎêòÍ≥†, Ìï¥Îãπ Í∞íÏù¥ v-model Í∞íÏúºÎ°ú Ï∂úÎ†•Îê®
                checkedData.push(this.val);
                console.log('-----', this.value, typeof(this.value));
                this.$emit('input', checkedData);
                
            } else {
                // Ìï¥Ï†ú Í∞í arr ÏÇ≠Ï†ú(Î∞∞Ïó¥Ïóê, ÎÇ¥Í∞Ä ÏÑ†ÌÉùÌïú val Í∞íÏù¥ ÏûàÏúºÎ©¥ Ï†úÍ±∞ÌïòÍ≥† Îúã Î±âÍ∏∞)
                // checkedData.filter(data => data != this.val);
                this.$emit('input', checkedData.filter(data => data != this.val));
            }
        }
    },
}
</script>
<style lang="scss">
  // [check]
.input-checkbox {
    .label-legend {
        position: relative;
        font-size: 1.6rem;
    }
    // Ïä§ÌÄòÏñ¥ Ïä§ÌÉÄÏùº
    &.type1 {
        .label-legend {
            line-height: 2.4rem;
            padding-left: 3rem;
            margin-bottom: 0.5em;
            &:before {
                @include pseudoBase();
                top: 0;
                bottom: 0;
                margin: auto;
            }
            &:before {
                @include itemSize(24px, 24px);
                left: 0;
                background-color: #fff;
                border: 1px solid #ccc;
                transition: all .15s;
            }
        }
        :checked + label {
            &:before {
                border: 7px solid $mainColor01;
            }
        }
    }
    // Ï≤¥ÌÅ¨ Ïä§ÌÉÄÏùº
    &.type2 {
        .label-legend {
            line-height: 2.4rem;
            padding-left: 3rem;
            margin-bottom: 0.5em;
            &:before,
            &:after {
                @include pseudoBase();
                top: 0;
                bottom: 0;
                margin: auto;
            }
            &:before {
                @include itemSize(24px, 24px);
                left: 0;
                background-color: $white;
                border: 1px solid $grayC;
                transition: all .15s;
            }
            &:after {
                @include itemSize(16px, 9px);
                top: -5px;
                left: 4px;
                border-left: 2.5px solid $white;
                border-bottom: 2.5px solid $white;
                border-color: $white;
                transform: rotate(315deg);
            }
        }
        :checked + label {
            &:before {
                background-color: $mainColor01;
            }
            &:after {
                border-color: $white;
            }
        }
    }
}
</style>