  data: () => ({
    // v-model Í∞êÏãúÍ∞í
    // Îç∞Ïù¥ÌÑ∞ ÎÑòÍ∏∏ Í∞í
    // `v-model`ÏùÄ Î™®Îì† form ÏóòÎ¶¨Î®ºÌä∏Ïùò Ï¥àÍ∏∞ `value`ÏôÄ `checked` Í∑∏Î¶¨Í≥† `selected` ÏÜçÏÑ±ÏùÑ Î¨¥ÏãúÌï©ÎãàÎã§. Ìï≠ÏÉÅ Vue Ïù∏Ïä§ÌÑ¥Ïä§ Îç∞Ïù¥ÌÑ∞Î•º ÏõêÎ≥∏ ÏÜåÏä§Î°ú Ï∑®Í∏âÌï©ÎãàÎã§. Ïª¥Ìè¨ÎÑåÌä∏Ïùò `data` ÏòµÏÖò ÏïàÏóê ÏûàÎäî JavaScriptÏóêÏÑú Ï¥àÍ∏∞Í∞íÏùÑ ÏÑ†Ïñ∏Ìï¥ÏïºÌï©ÎãàÎã§.
//     text ÏôÄ textarea ÌÉúÍ∑∏Îäî valueÏÜçÏÑ±Í≥º inputÏù¥Î≤§Ìä∏Î•º ÏÇ¨Ïö©Ìï©ÎãàÎã§.
// Ï≤¥ÌÅ¨Î∞ïÏä§Îì§Í≥º ÎùºÎîîÏò§Î≤ÑÌäºÎì§ÏùÄ checked ÏÜçÏÑ±Í≥º change Ïù¥Î≤§Ìä∏Î•º ÏÇ¨Ïö©Ìï©ÎãàÎã§.
// Select ÌÉúÍ∑∏Îäî valueÎ•º propÏúºÎ°ú, changeÎ•º Ïù¥Î≤§Ìä∏Î°ú ÏÇ¨Ïö©Ìï©ÎãàÎã§.
    guideRequest : {
