    // ÏóêÎü¨Îäî Î¨¥ÏßÑÏû• Îú®ÎÑ§ Î©ãÎ™®Î•¥Í≥† ÎßâÌïòÎãàÍπê(Î£®ÌîÑ Ïò§Î•ò Ï†úÏùºÏã¨Í∞Å)
    // arrow Ìï®Ïàò
    // (Ïù∏Ïàò) => return
    // [arrFunc, arrFunc, arrFun ......]
    // Í∞íÏù¥ Î∞îÎÄîÎïåÎßàÎã§ Ìï¥Îãπ rulse Îì§ ÏàúÌôòÌï¥Ï§òÏïºÌï®.
    // add them as functions returning true/error message.
    rules: {
