      // !false = true
      // !true = false
      // Ïôú Ïù¥Ï§ëÎ∂ÄÏ†ïÏùÑ ÌïòÏßÄ? Ïñ¥ÏßúÌîº Ïù¥Ï§ëÎ∂ÄÏ†ïÏùÄ ÏõêÍ∞í ÏïÑÎãåÍ∞Ä? Î≠î Ïù¥Ïú†Í∞Ä ÏûàÏúºÎãàÍπê Í±îÎÑ§Îì§Ïù¥ ÌñàÍ≤†ÏßÄ?
      text: [
        value => !!value || 'Required.',
