<template>
  <div :class="[`input-${inputType}`, `${styleType}`, `${className}`]">
    <!-- methods ÏÇ¨Ïö©Ìï†Îïå () ÍπåÏßÄ Ï†ÅÏñ¥ÏïºÌïúÎã§, Ïù∏Ïûê ÏóÜÏñ¥ÎèÑ ÎπºÎ©¥ ÏãúÌñâÏïàÎê® -->
    <label 
        v-if="!isLabelHide()"
        :for="id"
        class="label-legend"><slot /></label>

    <textarea 
        :id="id"
        :name="name"
        :placeholder="placeholder"
        :readonly="readonly"
        @input="onInputUpdate"/>
    <p><span>{{tmpValue.length}}</span> /10</p>
    <!-- [TODO]
        - ÏóêÎü¨Î¨∏Íµ¨ Ïª®Ìè¨ÎÑåÌä∏Ìôî
        - ÏóêÎü¨Î¨∏Íµ¨ Ïä§ÌÉÄÏùº
        - ÏóêÎü¨Î¨∏Íµ¨ Îì±Ïû•Ìö®Í≥º
     -->
    <!-- printErrMsg Í∞Ä Î†åÎçîÎßÅ ÎêòÏñ¥ÏïºÏßÄ Ïã§ÌñâÏù¥ ÎêúÎã§. Í∑∏ÎûòÏÑú v-ifÎ°ú Î™ªÌñàÎäîÎç∞ v-ifÎ°ú ÌïòÎäî Î∞©Î≤ïÏù¥ Î∂ÑÎ™Ö ÏûàÍ≤†ÏßÄ.... -->
    <p v-show="this.tmpErrMsg" class="error"><i></i>{{printErrMsg()}}</p>
  </div>
</template>
<script>
export default {
    name: 'InputTextarea',
    data() {
        return {
            cntText: 0,
            tmpValue: '',
            tmpErrMsg: null
        }
    },
    props: {
        id: {
            type: String,
            default: 'tmp'
        },
        name: {
            type: String,
            default: 'tmp'
        },
        inputType: {
            type: String,
            default: 'text'
        },
        styleType: {
            type: String,
            default: 'type1'
        },
        className: {
            type: String,
            default: null
        },
        placeholder: {
            type: String,
            default: 'ÌÖçÏä§Ìä∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî'
        },
        readonly: {
            type: Boolean,
            default: false
        },
        value: {
            type: String,
        },
        rules: {
            type: Array,
        }
    },
    methods: {
        isLabelHide: function() {
            switch (this.styleType) {
                case 'type3':
                    return true;
                default:
                    return false;
            }
        },
        onInputUpdate: function(e) {
            // this.value = e.target.value
            // console.log(e.target.value, e.target.value.length);
            // console.log("this.value", this.value)
            // ÏûêÏãùÏóêÏÑú ÏûÖÎ†•Îêú Í∞íÏùÑ Î∂ÄÎ™® Ïù¥Î≤§Ìä∏Î°ú Î≥¥ÎÉÑ
            // this.checkRules(value)
            // this.cntText = e.target.value.length
            this.tmpValue = e.target.value;
            this.$emit('input', e.target.value);
            // this.printErrMsg(this.rules);
        },
        checkRules: function() {
            // console.log(this.rules);
            let tmpValue = this.value;
            let tmpResult = this.rules.map(function(elem) {
                // console.log("foreach", elem(tmpValue), typeof(elem(tmpValue)));
                return elem(tmpValue);
                // elem('test');
            });
            // console.log("Í≤∞Í≥º", tmpResult);
            // method chainning ÌïòÎ†§Í≥† Î∂ÄÎìùÏù¥..
            return tmpResult;
        },
        printErrMsg: function() {
            this.tmpErrMsg = this.checkRules().find(element=> element !== true );
            // console.log("print============", this.tmpErrMsg);
            return this.tmpErrMsg;
        }
    }
}
</script>
<style lang="scss">
.input-textarea {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    textarea {
        min-height: 100px;
        font-size: 1.6rem;
        border: 1px solid rgb(221, 221, 221);
        padding: 1.5rem;
        line-height: 1.2;
        -ms-overflow-style: none;
        &::-webkit-scrollbar{
            display:none;
        }
    }
    .label-legend {
        font-size: 1.6rem;
        margin-bottom: 0.5em;
    }
    &.type1 {
        .label-legend {
            display: block;
            
        }
    }
    &.type2 {
        .label-legend {
            // justify Ïù¥Í≤ÉÏ†ÄÍ≤É Îã§ ÌñàÎäîÎç∞ Ïôú Ï†ÅÏö© Ïôú ÏïàÎèº..
            width: 100px;
            align-self: flex-start;
            text-align: right;
            padding-right: 1.5rem;
        }
        textarea {
            width: calc(100% - 100px);
        }
        .error {
            margin-left: 10rem;
        }
    }
}
// [TODO] ÏóêÎü¨ Ïª¥Ìè¨ÎÑåÌä∏Ïóê ÎÑ£Í∏∞
.error {
    position: relative;
    display: block;
    margin-top: 1.5rem;
    color: $errColor01;
}
</style>